
import axios from 'axios'
import { mapActions, mapState } from 'vuex'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Box from 'vux/src/components/box/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'

import creditCard from './../../../components/creditCard/creditCard'
import repay_01 from '@/assets/images/pay/repay_01.png'
import repay_02 from '@/assets/images/pay/repay_02.png'
import repay_03 from '@/assets/images/pay/repay_03.png'

export default {
name: "debitCard",
data() {
return {
users:localStorage.getItem('users')?JSON.parse(localStorage.getItem('users')):'',
bankCard:[],
index: 0,
checked:1,
demo2: '美食',
list2:['银行卡', '信用卡'],
creditCard:[],
idcard:'',
status:''

}

},
components: {
XHeader,Cell,Group,Box,XButton,CheckIcon, Swipeout, SwipeoutItem, SwipeoutButton,Tab, TabItem, Sticky, Divider, Swiper, SwiperItem , creditCard
},
created() {
this.getCaredList()
},
mounted() {
if(localStorage.getItem('Token')){
this.getCreditCard();
}
},
computed: mapState([
'card'
]),
watch: {
'card.creditCard'(){
this.creditCard = this.card.creditCard;
this.creditCard.map(v=>v['path']='/kbb/repay');
},
'card.msg'(){

},
},
methods: {
...mapActions([
'getCreditCard'
]),
getCaredList(){
this.$axios.post('/api/api/seeBank').then(response=>{
if(response.code==1){
this.bankCard = response.result;

}else{
mui.toast(response.msg)
}
}).catch(err=>{

});
},addCreditCard(){

if(this.users.is_validate==1) {
this.$router.push('/kbb/addCreditCard')
}else{
const {userId, session_id} = this.users;

this.$axios.post('/api/Usercenter/see_realname_apply',`data=${JSON.stringify({userId, session_id})}`)
.then(res=>{
if(res.code==200){
if(res.data.status==1){  //已实名
this.$router.push('/kbb/addCreditCard')
}else if(res.data.status==0){ //审核中
mui.toast('您的实名信息正在审核中')
this.$router.push('/my/my-name')
}else if(res.data.status==-1){ //被拒绝
mui.toast('需要先进行实名认证')
this.$router.push('/my/my-name')
}
else{
mui.toast('需要先进行实名认证')
this.$router.push('/my/my-name')
}
}else{
mui.toast('需要先进行实名认证')
this.$router.push('/my/my-name')
}
})
}
},
change_state(index,id){


this.$axios.post("/api/api/choiceBank", `data=${JSON.stringify({bankKey:id})}`).then(response=>{
if(response.code==1){
mui.toast(response.msg)
this.getCaredList();
}else{
mui.toast(response.msg)
}
}).catch(err=>{

});
},
onButtonClick(id){
const _this = this;
this.$vux.confirm.show({
title: '确认删除？',
onCancel () {
},
onConfirm () {
_this.$axios.post('/api/Partner/delBank',`data=${JSON.stringify({id})}`)
.then(res=>{
mui.toast(res.msg)
if(res.code==1){
_this.getCaredList()
}
})
}
})
}
}
}



